import React, { Fragment, ReactElement, useContext, useEffect, useState } from "react";
import { ThemeEditorContext } from "../ThemeEditor";
import { ElementLocator } from "./ElementLocator";
import { useDispatcher } from '../../hooks/useResumableReducer';
import { toPath } from '../../functions/nodePath';
import { ScrollInViewButton } from '../inspector/ScrollInViewButton';

export function PerformanceChecker({element = null}) {
  const { frameRef } = useContext(ThemeEditorContext);

  const [results, setResults] = useState(null as null | CheckResult[]);

  function doChecks() {
    const doc = element || frameRef.current.contentDocument.documentElement;
    const newResults = checks.map((check) => check(doc));

    setResults(newResults);
  }

  // useEffect(() => {
  //   setTimeout(() => {
  //     doChecks();
  //   }, 1000);
  // }, []);

  return (
    <Fragment>
      <button onClick={doChecks}>Sanity checks...</button>
      {results && (
        <Fragment>
          <ul>
            {results.map(([label, passes, details]) => {
              return (
                <li
                  key={label}
                  style={{ paddingBottom: "2rem", overflow: "hidden" }}
                >
                  {passes ? "✅" : "❌"} {label}
                  {details}
                </li>
              );
            })}
          </ul>
          <p>
            All checks exclude fully invisble elements, like <code>option</code> and <code>symbol</code>.
          </p>
          <br />
        </Fragment>
      )}
    </Fragment>
  );
}

PerformanceChecker.fName = 'PerformanceChecker';

type CheckResult = [label: string, passes: boolean, details?: ReactElement];

type PerformanceCheck = (element: HTMLElement) => CheckResult;

const relevantElements = 'body :not(option, symbol)';

const checks: PerformanceCheck[] = [

  (element) => {
    const allElements = element.querySelectorAll(relevantElements).length;
    const selector = 'body :has(> :nth-child(1)):not(option, symbol, :has(> :nth-child(2)))';
    const elementsWithExactlyOneDescendant = element.querySelectorAll(selector).length;
    const portion = elementsWithExactlyOneDescendant / allElements;

    return [
      'Uses wrapper elements sparingly',
      portion < 0.5,
      <Fragment>
        <div>
          {Math.round(portion * 100)}% of all elements (
          {elementsWithExactlyOneDescendant}/{allElements})
        </div>
        <ElementLocator
          {...{ selector, label: 'Elements with exactly one descendant',showLabel: true, property: null, allowScroll: true, instant: true }}
        />
      </Fragment>,
    ];
  },

  (element) => {
    // Exclude elements which generally are not used in the style calculation, like option elements inside a select.
    // Large lists of options are really common and skew the results.
    const elements = element.querySelectorAll(relevantElements);

    let sum = 0, deepestElement = null, deepestDepth = 0;
    for (const element of elements) {
      let depth = 0, parent = element;
      while (parent.nodeName !== 'BODY') {
        depth++;
        parent = parent.parentNode;
      }
      if (deepestElement === null || depth > deepestDepth) {
        deepestDepth = depth;
        deepestElement = element;
      }
      sum += depth;
    }

    const averageDepth = Math.round(sum / elements.length);
    const mark = 10;
    const passes = averageDepth <= mark;
    const path = deepestElement && toPath(deepestElement);

    return [
      "Sane average element depth",
      passes,
      <Fragment>
        <div title="Depth is distance from body element">
          Average depth(~{Math.round(averageDepth)}) is {passes ? "not " : " "}
          more than {mark}
        </div>
        {deepestElement && (
          <Fragment>
            <button
              onClick={() =>
                useDispatcher("inspectedPath")((path))
              }
            >
              Inspect deepest ({deepestDepth})
            </button>
            <ScrollInViewButton {...{path}}/>
          </Fragment>
        )}
      </Fragment>,
    ];
  },

  // (element) => {
  //   const chars = element.innerText.length;
  //   const elCount = element.querySelectorAll(relevantElements).length;

  //   const ratio = chars / elCount;
  //   const passes = ratio > 2;

  //   return [
  //     'Character to element ratio',
  //     passes,
  //     <Fragment>
  //       <br />
  //       {passes && <span>More than 2 characters per element</span>}
  //       {!passes && <span>Less than 2 characters per element</span>}
  //       <br />
  //       {chars} characters
  //       <br />
  //       {elCount} elements
  //       <br />
  //       {ratio.toFixed(2)} characters per element
  //     </Fragment>
  //   ];
  // }

];
